
















































@import '@design';

.container {
  display: flex;
  justify-content: space-between;
}

.listContainer {
  padding: 0;
  text-align: right;
  list-style-type: none;

  > li {
    display: inline-block;
    margin-right: $size-grid-padding;
  }
}

.logoContainer {
  display: flex;
  align-items: center;

  img {
    width: 52px;
    height: 52px;
    margin-top: -25px;
  }

  h1 {
    margin-top: 0;
    margin-left: 10px;
  }

  span {
    color: #a28939;
  }
}
